import React from 'react';
import { Layout, Divider } from 'antd';
import "./ContentView.less";

export default class ContentView extends React.Component {

    render() {
        const { intl, initDone } = this.props;

        const SloganView = () => {
            return (
                <div className='slogan-view'>
                    <div className='slogan'>
                        <span >国家乒乓球队大数据平台</span>
                    </div>
                    <div className='slogan-sub'>
                        <span>赛场数据快报系统</span>
                    </div>
                    {/* <div style={{ position: 'relative', width: '100%', height: '1px' }}>
                        <div className='decorationImage'></div>
                        <div className='customDivider'></div>
                    </div> */}
                </div>
            )
        };

        return (
            <div className='ContentView' >
                <SloganView />
                <Layout style={{ marginTop: "20px", width: "100%", backgroundColor: "white" }}>
                    {
                        this.props.children
                    }
                </Layout>
            </div>
        )
    }
}